.products {
	//margin-bottom: 85px
	margin-bottom: 55px;
	&.best-price {
		position: relative;
		padding-top: 2rem;
		margin-top: -45px;
		&:before {
			content: "";
			display: block;
			max-width: 1830px;
			width: 100%;
			min-height: 305px;
			margin: 0 auto;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			z-index: -1;
			background-color: var(--vinishkotyan); }
		@media #{$mobile} {
			.products__item {
				margin-bottom: 0;
				flex-basis: 50%;
				max-width: 50%;
				min-width: 50%;
				width: 100%; }
			&:before {
				min-height: 190px; } } }
	&-catalog {
		padding: 0 15px;
		&-by-time {
			display: flex;
			flex-wrap: wrap;
			width: 100%;
			margin-bottom: 65px;
			.by-time-header {
				//margin: 0 -15px
				margin-bottom: 35px; } }
		&__list {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;
			padding-top: 5px; }
		.express {
			flex-basis: 100%;
			margin-bottom: 45px;
			&-block {
				flex-direction: row;
				align-items: center;
				padding: 25px 65px; }
			&__icon {
				margin-right: 20px; }
			&__title {
				margin-bottom: 10px; }
			&__subtitle {
				margin: 0; }
			&__form {
				margin-left: auto;
				max-width: 354px;
				width: 100%; }
			&__address {
				display: none; }
			&.submited {
				//.express__icon,
				//.express__subtitle,
				//.express__form
				//	display: none
				.express__text {
					display: flex;
					align-items: center; }
				.express__title {
					font-size: 1rem;
					font-weight: bold;
					margin-bottom: 0;
					margin-right: 15px; }
				.express__address {
					margin-right: 20px; } }

			@media #{$desktop} {
				&-block {
					padding: 20px; }
				&-title {
					font-size: 1rem;
					line-height: 1.5rem; }
				&__form {
					max-width: 300px; }
				&__text {
					flex-wrap: wrap; } }

			@media #{$tablet} {
				margin-bottom: 30px;
				&-block {
					flex-wrap: wrap; }
				&__form {
					max-width: 100%;
					margin-top: 25px; } }

			@media #{$mobile} {
				&-block {
					padding: 25px 15px 15px; }
				&__icon {
					display: none; }
				&-title {
					font-size: 1.125rem;
					line-height: 1.5rem; } } } }

	&-hit {
		.products__item {
			flex-basis: 100%;
			max-width: 100%; }
		.products__item__inner {
			max-width: 100%; } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
		//margin-bottom: 30px

		&.scroll-x {
			flex-wrap: nowrap;
			margin: 0; } }

	&__more {
		display: flex;
		flex-basis: 100%;
		align-items: center;
		justify-content: center; }

	&__item {
		flex-basis: 25%;
		max-width: 25%;
		width: 100%;
		padding: 0 15px;
		position: relative;
		//padding-bottom: 50px
		padding-bottom: 20px;
		&:not(:nth-last-child(-n+3)) {
			margin-bottom: 70px; }
		@media #{$laptop} {
			flex-basis: 33.3%;
			max-width: 33.3%;
			margin-bottom: 30px; }
		@media #{$mobile} {
			flex-basis: 100%;
			max-width: 100%;
			min-width: 300px;
			margin-top: 5px;
			margin-bottom: 0;
			&__price {
				margin-right: 15px; }
			//&:first-child
			//argin-left: -15px
			//&:last-child
 }			//argin-right: -15px
		@media #{$mobile-xs} {
			min-width: 260px; }


		&__inner {
			background-color: var(--white);
			max-width: 278px;
			margin: 0 auto;
			@media #{$mobile} {
				background: none; } }
		&__size {
			&__big {
				flex-basis: 50%;
				max-width: 50%;
				margin-bottom: 30px;
				position: relative;
				.products__item {
					flex-basis: 100%;
					max-width: 100%; }
				.products__item__img {
					margin-bottom: 0; }
				.swiper-button__wrapper {
					position: absolute;
					right: 45px;
					top: 30px;
					min-width: 90px; }
				.swiper-wrapper {
					padding-top: 5px; }
				.swiper-button-prev {
					background-image: url("data:image/svg+xml,%3Csvg width='47' height='16' viewBox='0 0 47 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.29289 15.7071C7.68342 16.0976 8.31658 16.0976 8.70711 15.7071C9.09763 15.3166 9.09763 14.6834 8.70711 14.2929L3.44546 9.03124L46 9.03124C46.5523 9.03124 47 8.58352 47 8.03124C47 7.47895 46.5523 7.03124 46 7.03124L3.38297 7.03124L8.70711 1.7071C9.09763 1.31658 9.09763 0.683414 8.70711 0.29289C8.31658 -0.0976348 7.68342 -0.0976349 7.29289 0.29289L0.292893 7.29289C0.0936821 7.4921 -0.00390934 7.75446 0.000122768 8.01553C4.26592e-05 8.02076 6.97109e-07 8.02599 6.96651e-07 8.03124C6.6588e-07 8.38322 0.181851 8.69273 0.456723 8.87093L7.29289 15.7071Z' fill='white'/%3E%3C/svg%3E%0A") !important; }
				.swiper-button-next {
					background-image: url("data:image/svg+xml,%3Csvg width='47' height='16' viewBox='0 0 47 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M39.7071 0.292893C39.3166 -0.0976311 38.6834 -0.0976311 38.2929 0.292893C37.9024 0.683418 37.9024 1.31658 38.2929 1.70711L43.5545 6.96875L1 6.96876C0.447715 6.96876 0 7.41647 0 7.96876C0 8.52104 0.447715 8.96876 1 8.96876L43.617 8.96875L38.2929 14.2929C37.9024 14.6834 37.9024 15.3166 38.2929 15.7071C38.6834 16.0976 39.3166 16.0976 39.7071 15.7071L46.7071 8.70711C46.9063 8.50789 47.0039 8.24554 46.9999 7.98446C47 7.97924 47 7.974 47 7.96875C47 7.61677 46.8181 7.30726 46.5433 7.12905L39.7071 0.292893Z' fill='white'/%3E%3C/svg%3E%0A") !important; }
				.products__item__inner {
					max-width: 100%; }
				.products__item__info {
					background-color: var(--beige);
					padding: 12px 45px 15px 45px;
					flex-direction: row;
					align-items: center;
					@media #{$laptop} {
						padding: 12px 10px 15px 10px; }
					.products__item__name {
						max-width: 200px;
						margin-bottom: 0; }
					.products__item__price__cart {
						align-items: center;
						justify-content: flex-end;
						max-width: 280px;
						width: 100%; }
					.products__item__cart__link {
						width: auto;
						padding: 0 13px 2px 13px; }
					.products__item__cart__link__text {
						display: inline-block;
						color: var(--yellow); }
					.icon-product_add_to_cart {
						fill: var(--yellow); } } }

			@media #{$tablet} {
				&__big {
					flex-basis: 66.6%;
					max-width: 66.6%; } }
			@media #{$mobile} {
				&__big {
					display: none;
					//flex-basis: 100%
 } } }					//max-width: 100%


		&__slider {
			.swiper {
				&-container {}
				&-button {
					&__wrapper {
						position: absolute;
						top: 30px;
						right: 30px;
						display: flex;
						align-items: center;
						flex-wrap: wrap;
						justify-content: space-between;
						width: 86px; }
					&-next {
						background-image: url("data:image/svg+xml,%3Csvg width='48' height='16' viewBox='0 0 48 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.0507813 7.96875C0.0507813 7.41647 0.498496 6.96875 1.05078 6.96875L46.0508 6.96875C46.6031 6.96875 47.0508 7.41646 47.0508 7.96875C47.0508 8.52103 46.6031 8.96875 46.0508 8.96875L1.05078 8.96875C0.498497 8.96875 0.0507814 8.52103 0.0507813 7.96875Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M38.3437 0.292893C38.7342 -0.0976311 39.3674 -0.0976311 39.7579 0.292893L46.7579 7.29289C47.1484 7.68342 47.1484 8.31658 46.7579 8.70711L39.7579 15.7071C39.3674 16.0976 38.7342 16.0976 38.3437 15.7071C37.9532 15.3166 37.9532 14.6834 38.3437 14.2929L44.6366 8L38.3437 1.70711C37.9532 1.31658 37.9532 0.683417 38.3437 0.292893Z' fill='white'/%3E%3C/svg%3E%0A");
						margin-left: auto; }
					&-prev {
						background-image: url("data:image/svg+xml,%3Csvg width='48' height='16' viewBox='0 0 48 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M47.0508 8.03125C47.0508 8.58353 46.6031 9.03125 46.0508 9.03125H1.05078C0.498497 9.03125 0.0507812 8.58353 0.0507812 8.03125C0.0507812 7.47897 0.498497 7.03125 1.05078 7.03125H46.0508C46.6031 7.03125 47.0508 7.47897 47.0508 8.03125Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.75789 15.7071C8.36736 16.0976 7.7342 16.0976 7.34367 15.7071L0.343675 8.70711C-0.0468486 8.31658 -0.0468485 7.68342 0.343675 7.29289L7.34368 0.292893C7.7342 -0.0976297 8.36737 -0.0976296 8.75789 0.292893C9.14841 0.683417 9.14841 1.31658 8.75789 1.70711L2.465 8L8.75789 14.2929C9.14841 14.6834 9.14841 15.3166 8.75789 15.7071Z' fill='white'/%3E%3C/svg%3E%0A");
						margin-right: auto; } } } }

		&__slide {
			&__link,
			&__img {
				display: block;
				font-size: 0;
				line-height: 0;
				max-width: 100%;
				height: auto; }
			&__img {
				margin: 0 auto;
				@media #{$laptop} {
					padding-top: 9px; }
				@media #{$mobile} {
					padding-top: 0; } } }


		&__label__wrap {
			position: absolute;
			top: -5px;
			left: 7px;
			z-index: 1;
			display: flex;
			flex-wrap: wrap; }
		&__label {
			background: #fff;
			padding: 5px 8px;
			font-family: 'Roboto';
			font-size: 0.75rem;
			line-height: 0.875rem;
			font-weight: bold;
			display: inline-block;
			margin-bottom: 2px;
			svg {
				margin-right: 3px; }
			&.hit {
				color: #DF4C4C; }
			&.novelty {
				color: var(--badge-yellow); }
			&.vegan {
				color: var(--badge-green); }
			&.timer {
				background-color: var(--badge-yellow);
				color: var(--white); }
			&.no-gmo {
				color: var(--badge-dark-green); }
			&.no-gluten {
				color: var(--badge-blue); }
			&.sale {
				color: var(--black); } }
		&__img {
			margin-bottom: 15px;
			@media #{$tablet} {
				margin-bottom: 10px; } }
		&__info {
			display: flex;
			justify-content: space-between;
			flex-direction: column;
			position: relative; }
		&__name {
			//max-width: 180px
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			text-overflow: ellipsis;
			-webkit-box-orient: vertical;

			font-size: 1.125rem;
			line-height: 1.375rem;
			color: var(--black);

			margin-bottom: 6px;
			@media #{$mobile} {
				font-size: 1rem;
				line-height: 1.188rem; } }
		&__excerpt {
			font-size: 0.875rem;
			line-height: 1.125rem;
			color: #657082;
			opacity: 0.8;
			margin-bottom: 15px;

			min-height: 55px;
			max-height: 55px;

			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 3;
			text-overflow: ellipsis;
			-webkit-box-orient: vertical;
			@media #{$mobile} {
				display: none; } }
		&__price__cart {
			display: flex;
			justify-content: space-between; }
		&__price {
			display: flex;
			align-items: center;
			margin-right: 35px;
			@media #{$mobile} {
				margin-right: 0; }
			&__normal {
				font-weight: bold;
				font-size: 1.25rem;
				line-height: 1.5rem;
				color: var(--vinishkotyan);
				&:last-child {
					color: var(--black); }
				@media #{$mobile} {
					font-size: 1rem;
					line-height: 1.313rem; } }
			&__sale {
				font-size: 0.875rem;
				line-height: 1.063rem;
				color: var(--black);
				margin-left: 8px;
				text-decoration: line-through;
				&:first-child {
					margin-left: 0; } } }
		&__cart {
			&__link {
				padding: 0 8.5px 2px 8.5px;
				height: 36px;
				//width: 36px
				display: flex;
				align-items: center;
				justify-content: center;
				&__text {
					display: none;
					margin-right: 8px;
					font-size: 0.875rem;
					line-height: 1.125rem; }
				&__icon {
					height: 18px;
					.icon-product_add_to_cart {}
					.icon-product_in_cart {
						display: none; } }
				&:hover {
					background-color: var(--vinishkotyan);
					width: auto;
 }					//transition: .5s
				&:hover {
					.products__item__cart__link__text {
						display: inline-block;
						color: var(--yellow);
						@media #{$mobile} {
							display: none; } }
					.icon-product_add_to_cart {
						fill: var(--yellow); }
					.products-pickup {
						display: flex; } }
				&.in-cart,
				&.active {
					width: auto;
					background-color: var(--light-pink);
					.products__item__cart__link__text {
						display: inline-block;
						color: var(--vinishkotyan); }
					.icon-product_add_to_cart {
						display: none; }
					.icon-product_in_cart {
						display: inline-block; }
					@media #{$mobile} {
						.products__item__cart__link__text {
							display: none !important; } } } } } }


	&-pickup {
		padding: 40px 50px;
		min-width: 315px;
		display: none;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		position: absolute;
		right: 0;
		bottom: 50px;
		z-index: 2;
		background: #FFFFFF;
		box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
		&:after {
			content: "";
			background-image: url("data:image/svg+xml,%3Csvg width='17' height='10' viewBox='0 0 17 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.16731 0.37207L16.2838 9.99989H0.0507812L8.16731 0.37207Z' fill='white'/%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-position: top center;
			display: block;
			width: 17px;
			height: 10px;
			filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.09));
			position: absolute; }
		&__icon {
			margin-bottom: 20px; }
		&__title {
			font-size: 1.125rem;
			font-weight: bold;
			line-height: 1;
			color: var(--black);
			margin-bottom: 15px; }
		&__text {
			font-size: 0.875rem;
			font-weight: normal;
			color: #1A1A1A; }

		@media #{$mobile-xs} {
			min-width: 290px;
			max-width: 290px; } } }


@media #{$mobile} {
	.products__item:nth-child(odd) {
		.products-pickup {
			right: initial;
			left: 0; } } }
@media #{$mobile} {
	.section.products {
		.section-content.scroll-x {
			padding: 30px 0; }
		.products__item__info {
			position: initial; }
		.products-pickup {
			left: 25px;
			right: 0;
			bottom: 0;
			min-width: auto;
			//max-width: none
			padding: 20px;
			max-width: 270px;
			width: 200%;
			position: fixed;
			top: 30%;
			max-height: 250px;
			z-index: 9999;
			transition: 1s; } } }


