.news {
	@media #{$laptop-above} {
 }		//margin-bottom: 130px
	&-list {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		margin: 0 -15px;
		.descr {
			flex-basis: 100%;
			margin-bottom: 65px; } }
	&-item {
		flex-basis: 33.333%;
		max-width: 33.333%;
		width: 100%;
		padding: 0 15px;
		padding-bottom: 20px;
		position: relative;
		margin-bottom: 50px;
		&__header,
		&__body,
		&__name {
			margin-bottom: 30px; }
		&__name {
			font-weight: bold;
			display: block;
			&:hover {
				cursor: pointer;
				color: var(--vinishkotyan); } }
		&__body {
			display: block; }
		&__excerpt {
			font-size: 1rem;
			line-height: 1.5rem; }
		&__link {
			display: block; }
		&__img {
			display: block;
			max-width: 100%;
			max-height: 100%; }
		&__date {
			font-size: 1.125rem;
			line-height: 1.438rem;
			font-weight: bold; }
		&__footer {} }



	@media #{$mobile} {
		&-item {
			flex-basis: 50%;
			max-width: 50%;
			&:not(:last-child) {
				margin-bottom: 30px; } } }

	@media #{$mobile-xs} {
		&-list {
			flex-wrap: nowrap;
			margin: 0; }
		&-item {
			flex-basis: 100%;
			max-width: 100%;
			min-width: 250px;
			margin-bottom: 0 !important;
			&__body {
				margin-bottom: 15px; }
			&__name {
				font-size: 1.125rem;
				line-height: 1.375rem;
				margin-bottom: 15px; }
			&__excerpt {
				line-height: 1.375rem; } } } }

