@media(min-width: 1440px) {
	.container, .container-sm, .container-md, .container-lg, .container-xl {
		max-width: 1200px; } }



.link-icon {
	display: inline-flex;
	align-items: center;

	&__icon {
		margin-right: 6px; }

	&__text {
		font-size: 0.813rem;
		line-height: 0.813rem;
		color: var(--black);
		border-bottom: 1px dotted;
		white-space: nowrap; } }


.button {
