input::-webkit-contacts-auto-fill-button {
	visibility: hidden;
	pointer-events: none;
	position: absolute;
	right: 0; }

input::-ms-clear {
	width: 0;
	height: 0; }


.field {
	position: relative;

	&:not(:last-child) {
		margin-bottom: 10px; } }

.input {
	width: 100%;
	height: 44px;
	color: #1b1e32;
	font-size: 17px;
	background: transparent;
	border: none;
	border-bottom: 1px solid #e7e8e9; }

.range-control {
	display: flex;
	align-items: center;
	cursor: pointer;

	&:not(.is_checked) {
		.range-control__title {
			&:first-child {
				opacity: 1; } } }

	&.is_checked {
		.range-control__icon {
			&:before {
				transform: translate3d(30px, 0, 0);

				@media #{$mobile} {
					transform: translate3d(74px, 0, 0); } } }

		.range-control__title {
			&:last-child {
				opacity: 1; } } }

	&__icon {
		width: 55px;
		height: 24px;
		border-radius: 12px;
		background-color: #edeef1;
		margin: 0 10px;
		position: relative;

		@media #{$mobile} {
			width: 100px; }

		&:before {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 7px;
			margin: auto;
			width: 12px;
			height: 12px;
			background-color: #33394a;
			border-radius: 50%;
			transition: 300ms ease-out; } }

	&__title {
		color: #3a4051;
		font-size: 16px;
		font-weight: 500;
		opacity: 0.6;
		text-transform: uppercase;
		letter-spacing: 1.6px;
		transition: 300ms ease-out;

		@media #{$mobile} {
			font-size: 14px;
			position: relative;
			top: 1px; } } }



