.modal {
	display: none;
	background-color: var(--light-pink);
	width: 100%;
	height: 100%;
	&__custom {
		.fancybox-bg {
			background-color: #fff;
			opacity: 1; }
		.fancybox-slide {
			padding: 50px; } }
	&__inner {
		max-width: 400px;
		margin: 0 auto; }
	&__head {
		margin-bottom: 25px; }
	&__title {
		font-size: 2.25rem;
		line-height: 1;
		font-weight: bold; }
	&__text {
		font-size: 1.125rem;
		line-height: 1.688rem;
		letter-spacing: 0.01em; }
	&__title + &__text {
		margin-top: 10px; }
	&__body {}
	&__form {
		.checkbox__text a {
			color: var(--vinishkotyan); } }
	&__thx {
		text-align: center;
		&__icon {
			margin-bottom: 15px; }
		&__img {
			max-width: 100%;
			height: auto; } }
	.button {
		margin-top: 40px;
		min-width: 220px; }
	.form_submit {
		margin-top: 25px; }
	&.fancybox-content {
		@media #{$mobile-above} {
			display: inline-flex !important;
			align-items: center;
			justify-content: center; }
		&[id*="thx"] {
			display: inline-flex !important;
			align-items: center;
			justify-content: center; } }
	@media #{$mobile} {
		&__custom {
			.fancybox-slide {
				padding: 10px; }
			.fancybox-content {
				padding: 15px; } } } }
