.by-time {
	&-header {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		margin-bottom: 35px;
		&-title {
			display: flex;
			align-items: baseline;
			flex-basis: 100%;
			padding: 0 15px;
			&__icon {
				margin-right: 7px;
				max-height: 18px;
				position: relative;
				top: 3px;
				display: none; }
			&__text {
				font-weight: bold;
				font-size: 1.125rem;
				line-height: 1.438rem;
				//padding-left: 18px
				&:before {
					content: "";
					min-width: 18px;
					max-width: 18px;
					height: 19px;
					background-image: url("data:image/svg+xml,%3Csvg width='18' height='19' viewBox='0 0 18 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='9' cy='9.5' r='8' stroke='%23A70230' stroke-width='2'/%3E%3Cpath d='M8.40015 6.5V10.1H10.8001' stroke='%23A70230' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
					display: inline-block;
					margin-right: 7px; } } }
		.link-icon__text {
			color: var(--vinishkotyan); } }
	&-remove-order {
		margin-left: 20px;
		.icon-trash {
			fill: var(--vinishkotyan); } }
	&-btn {
		@media #{$mobile-above} {
			margin-left: 20px; }
		@media #{$mobile} {
			margin-top: 10px; }
		.button {
			height: 36px;
			padding: 8px 15px; } }
	@media #{$mobile} {
		&-header {
			&-title {
				flex-direction: column;
				//padding: 0
				&__text {
					font-size: 1rem;
					line-height: 1.5rem;
					display: flex;
					align-items: center; } } }
		&-remove-order {
			margin-left: 0;
			margin-top: 10px; } } }



.catalog-filter + script + [id*='bx_incl_area_'] {
