.header {
	margin-bottom: 55px;
	.container {
		padding: 0; }

	@media #{$mobile} {
		[data-move] {
			display: none; }
		&-mobile {
			[data-move] {
				display: flex; } } }

	@media #{$laptop} {
		margin-bottom: 30px; }

	@media #{$mobile-xs} {
		margin-bottom: 15px; } }

.header-city {
	.dropdown-block {
		&:after {
			left: 60px; } }
	@media #{$laptop} {
		.link-icon__text {
			max-width: 140px;
			display: inline;
			white-space: initial; } } }

.header-nav {
	margin-right: 5.7%;
	margin-left: auto;
	//margin-left: 20.6%
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		margin-right: 37px;
		&:last-child {
			margin-right: 0; }
		&__link {
			font-size: .875rem;
			font-weight: bold;
			line-height: 1.063rem;
			display: inline-flex; }
		.dropdown-menu {
			min-width: auto; } }

	@media #{$tablet} {
		& {
			margin-right: 10px; }
		&__item {
			margin-right: 10px;
			&__link {
				font-size: 0.813rem; } } } }

.header-call {
	display: flex;
	align-items: flex-end;
	@media #{$tablet} {
		align-items: center; } }

.header-link {
	&__list {
		display: flex;
		//margin-right: 2.5%
		margin-right: 25px; }
	&__item {
		&:not(:last-child) {
			margin-right: 25px; }
		.link-icon__text {
			white-space: nowrap; } }
	@media #{$tablet} {
		&__list {
			margin-right: 10px; }
		&__item {
			&:not(:last-child) {
				margin-right: 10px; }
			.link-icon__icon {
				display: none; }
			.link-icon__text {
				font-size: 0.75rem; } } } }

.header-phone {
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		&:not(:last-child) {
			margin-right: 18px; }
		&__label {
			font-size: 0.625rem;
			line-height: 0.813rem;
			display: flex;
			align-items: center;
			color: var(--grey-text); }
		&__link {
			font-size: 0.813rem;
			line-height: 1rem;
			font-weight: bold;
			color: var(--black);
			white-space: nowrap; } }
	@media #{$tablet} {
		&__item {
			&:not(:last-child) {
				margin-right: 10px; }
			&__label {
				display: none; }
			&__link {
				font-size: 0.75rem; } } } }

.header-burger {
	display: flex;
	align-items: center;
	margin-left: -15px;
	margin-right: 10px;
	&__link {
		padding: 10px; }
	.dropdown-block {
		&:after {
			left: 25px; } }
	@media #{$mobile} {
		display: none; } }

.header-logo {
	margin-right: 40px;
	&__img {
		max-width: 100%;
		height: auto; }
	@media #{$laptop} {
		margin-right: 25px; }

	@media #{$laptop} {
		margin-right: 15px;
		.icon-logo-with-text {
			width: 190px;
			height: 35px; } }
	@media #{$mobile} {
		display: none; } }

.header-search {
	max-width: 33%;
	width: 100%;
	//margin-right: 45px
	margin-right: auto;
	.dropdown-block {
		&:after {
			left: 40%; } }
	@media #{$desktop} {
		margin-right: 25px;
 }		//min-width: 335px

	@media #{$laptop} {
		//min-width: 270px
		margin-right: 15px; }

	@media #{$mobile} {
		display: none; } }

.form-search {
	&__form {
		width: 100%;
		max-width: 355px; }
	&__input {
		padding-right: 35px;
		border-radius: 25px;
		height: 35px !important; }
	&__button {
		background-color: var(--white);
		box-shadow: none;
		cursor: pointer; }
	&__icon {
		margin-left: -30px;
		.icon-search-new {
			width: 17px;
			height: 17px;
			stroke: var(--black); } } }

.header-bar {
	display: flex;
	&__item {
		display: flex;
		align-items: center;
		margin-right: 20px;
		.dropdown-block {
			&:after {
				left: 65px; } }
		&__icon {
			margin-right: 6px; }
		&__link {
			display: flex;
			align-items: center;

			font-size: 1rem;
			font-weight: bold;
			line-height: 1.188rem;
			color: var(--white);
			&:hover {
				color: var(--yellow); } }
		&__label {
			white-space: nowrap; } }

	@media #{$tablet} {
		&__item {
			position: initial !important;
			&__link {
				font-size: .875rem; }
			&__label {
				display: none; } } }

	@media #{$mobile} {
		&__item {
			&__label {
				display: inline-block; } } } }

.header-cart {
	display: flex;
	min-width: max-content;
	&__counter {
		display: flex;
		position: relative;
		margin-right: 15px;
		cursor: pointer; }
	&__icon {}
	&__number {
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 100%;
		background-color: var(--white);

		font-weight: bold;
		font-size: .75rem;
		line-height: 120%;
		text-align: center;
		color: var(--vinishkotyan);

		margin-top: -9px;
		margin-left: -2px; }

	&__price {
		font-size: 18px;
		font-weight: bold;
		line-height: 120%;
		color: var(--white); }

	@media #{$laptop} {
		&__counter {
			margin-right: 10px; }
		&__price {
			font-size: 14px; } }

	@media #{$mobile} {
		display: none;
		&__number {
			background-color: var(--vinishkotyan);
			color: var(--white); }
		&__counter {
			margin-right: 0; }
		&__number {
			margin-top: 0; } } }



.header-top {
	//background-color: #FAFBFD
	position: relative;
	max-width: 1230px;
	padding: 0 15px;
	margin: 0 auto;
	@media #{$desktop} {
		max-width: 1170px; }
	.grid-container {
		display: flex;
		align-items: flex-end;
		padding-top: 15px;
		padding-bottom: 12px; }


	.header-nav {
		&__item {
			color: var(--black); } } }

.header-main {
	position: relative;
	max-width: 1230px;
	padding: 0 15px;
	margin: 0 auto;
	@media #{$desktop} {
		max-width: 1170px; }
	.grid-container {
		display: flex;
		align-items: center;
		background-color: var(--vinishkotyan);
		padding: 10px 30px;
		@media #{$desktop} {
			padding: 10px 20px; } }

	.header-nav {
		margin-right: 50px;
		&__item {
			margin-right: 35px;
			.icon-menu {
				margin-left: 10px; } }
		&__link {
			font-size: 18px;
			font-weight: bold;
			line-height: 120%;
			color: var(--white); } }

